body {
  font-family: monospace;
  font-size: 24px;
  background-color: blanchedalmond;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  flex-direction: column;
}

h1 {
  font-size: 32px;
}

ul {
  list-style-type: square;
}

li:hover {
  font-weight: 700;
  cursor: default;
}
